import { Component, OnInit, ViewChild } from '@angular/core';
import { DateAdapter } from '@angular/material/core';
import { ActivatedRoute } from '@angular/router';
import { ajax, apiURL } from '../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';


import {MatTable} from '@angular/material';


interface EventInTable {
  title: string;
  debut: string;
  id: string;
  index: number;
}

interface SelectedEvent {
  country_id;
  event_cat_id;
  img_id;
  img_data;
  origin_id;
  pdf_id;
  pricing_id;
  region_id;
  city;
  comm_appeal;
  debut;
  description;
  description_short;
  end;
  infos_prat;
  is_approved;
  is_highlighted;
  is_public;
  is_sfse;
  link;
  more_infos;
  place;
  table_name;
  title;
  val_act;
}

@Component({
  selector: 'app-events',
  templateUrl: './events.component.html',
  styleUrls: ['./events.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})

export class EventsComponent implements OnInit {
  type: string;
  title: string;
  events = [];
  imgs = [];
  event_cats = [];
  pricings= [];
  regions = [];
  event_origins = [];
  countrys = [];
  eventType = {type: '', method:'read_without_img'};

  

  columnTitles: string[] = ['Titre'];
  columnsToDisplay: string[] = ['title'];
  expandedElement: EventInTable | null;
  selectedEvent: SelectedEvent | null;
  
  @ViewChild(MatTable) table: MatTable<any>;


  constructor(private router: ActivatedRoute, private _adapter: DateAdapter<any>) { }

  refresh() {
    this.table.renderRows();
  }

  editEvent(element) {
    ['debut', 'end', 'comm_appeal'].forEach(item => {
      if (this.selectedEvent[item]) {
        this.selectedEvent[item] = new Date(this.selectedEvent[item]).getTime();
      }
    });
    
    const url = apiURL('event', 'update', '');
    ajax(url, 'POST', this.selectedEvent, (res: string) => {
      this.events[element.index] = JSON.parse(res);
      this.expandedElement = null;
    }, false);
  }

  selectEvent(elementId) {
    const url = apiURL('event', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedEvent = JSON.parse(res).records;
      if (this.selectedEvent.img_data) {
        this.selectedEvent.img_data = 'data:image/jpg;base64,' + this.selectedEvent.img_data;
      } else {
        this.selectedEvent.img_data = '../../../../assets/images/sfse-logo-menu.mng.png'
      }
      /*
      ['debut', 'end', 'comm_appeal'].forEach(item => {
        if (this.selectedEvent[item]) {
          this.selectedEvent[item] = dateForAMDInput(this.selectedEvent[item]);
        }
      });
      */
    }, false);
  }

  setIndexToEvents(events) {
    if(!events || events.length < 1){ return; }
    events.forEach((event, index) => {
      event.index = index;
      this.events.push(event);
    });
  }

  loadItems(events){
    this.events = [];
    this.setIndexToEvents(events);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  ngOnInit() {
    this.router.params.subscribe(data => {
      this.type = data.type;

      if (this.type == 'commAppeal') {
        this.title = 'Appels à communication';
        this.eventType.type = 'comm_appeal';
      } else if (this.type == 'valAct') {
        this.title = 'Actes de valorisation';
        this.eventType.type = 'val_act';
      } else {
        this.title = 'Manifestations francophone';
        this.eventType.type = 'event';
      }
    });
    this._adapter.setLocale('fr');
  }

}

