import { Component, OnInit, ViewChild, Inject} from '@angular/core';
import { ajax, apiURL } from '../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {MatTable} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { MatConfirmComponent } from '../../mat-confirm.component';
import { DATA } from '../../../providers/data';

export interface SelectedThematic {
  name: string;
  id: string;
}

@Component({
  selector: 'app-thematics',
  templateUrl: './thematics.component.html',
  styleUrls: ['./thematics.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class ThematicsComponent implements OnInit {
  @ViewChild(MatTable) table: MatTable<any>;
  thematics   = [];

  newThematic = {};

  columnTitles: string[] = ['Nom'];
  columnsToDisplay: string[] = ['name'];
  expandedElement: any | null;
  selectedThematic: SelectedThematic | null;

  constructor(public dialog: MatDialog) {}

  openDialog(): void {
    const dialogRef = this.dialog.open(NewThematicComponent, {
      width: '300px'
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      this.newThematic = result;
    });
  }

  refresh() {
    this.table.renderRows();
  }

  edit(element) {
    const url = apiURL('thematic', 'update', '');
    ajax(url, 'POST', this.selectedThematic, (res: any) => {
      this.thematics[element.index] = this.selectedThematic;
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  selectThematic(elementId) {
    const url = apiURL('thematic', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedThematic = JSON.parse(res);
    }, false);
  }

  deleteThematic(index) {
    const url = apiURL('thematic', 'delete', '');
    ajax(url, 'POST', {id: this.selectedThematic.id}, (res: string) => {
      console.log(res);
      this.thematics.splice(index, 1);
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  confirmDeleteThematic(index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'supprimer cette thématique ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteThematic(index); }
    });
  }

  openCreateForm() {
    this.openDialog();
  }

  ngOnInit() {
    const url = apiURL('thematic', 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      this.thematics = JSON.parse(res).records.map((thematic, index) => {
        thematic.index = index;
        return thematic;
      });
      this.table.renderRows();
    }, false);
  }
}

export interface NewThematic {
  name: string;
}

@Component({
  selector: 'app-new-thematic',
  templateUrl: 'new-thematic.component.html',
  styleUrls: ['./thematics.component.css'],
})
export class NewThematicComponent {
  newThematic: NewThematic = {name: ''};
  
  constructor(
    public dialogRef: MatDialogRef<NewThematicComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {}

  closeForm(): void {
    this.dialogRef.close();
  }

  addNewThematic() {
    const url = apiURL('thematic', 'create', '');
    ajax(url, 'POST', this.newThematic, (res: any) => { 
      DATA.newThematic = this.newThematic;
    }, false);
  }

}
