import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ajax, apiURL } from '../../../providers/helpers';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DATA } from '../../../providers/data';
import { MatBottomSheetComponent } from '../../mat-bottom-sheet.component';
import { DateAdapter } from '@angular/material/core';

class Event {
  country_id;
  event_cat_id;
  img_id;
  origin_id;
  pdf_id;
  pricing_id;
  region_id;
  city;
  comm_appeal;
  debut;
  description;
  description_short;
  end;
  infos_prat;
  is_approved = '1';
  is_highlighted = '0';
  is_public = '1';
  is_sfse;
  link;
  more_infos;
  place;
  table_name = 'event';
  title;
  val_act;
}

@Component({
  selector: 'app-event-create',
  templateUrl: './event-create.component.html',
  styleUrls: ['./event-create.component.css']
})
export class EventCreateComponent implements OnInit {
  
  newItem;
  type = '';
  title = '';
  event_cats = [];
  pricings= [];
  regions = [];
  event_origins = [];
  countrys = [];

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  createEvent() {
    const messages = [];
   
    if (!this.newItem.country_id) {
      messages.push('Pays');
    }
    if(this.newItem.country_id == 73 && !this.newItem.region_id) {
      messages.push('Région en France');
    }
    if (!this.newItem.event_cat_id) {
      messages.push('Catégorie');
    }
    if (!this.newItem.origin_id) {
      messages.push('Organisme organisateur');
    }
    if (!this.newItem.pricing_id && this.type != 'valAct') {
      messages.push('Tarif');
    }
    if (!this.newItem.debut) {
      messages.push('Date de début');
    }
    if (!this.newItem.description) {
      messages.push('Description de l\'événement');
    }
    if (!this.newItem.description_short) {
      messages.push('Description courte');
    }
    if (!this.newItem.end) {
      messages.push('Date de fin');
    }
    if (!this.newItem.infos_prat) {
      messages.push('Infos pratiques');
    }
    if (!this.newItem.place) {
      messages.push('Lieu de l\'événement');
    }
    if (!this.newItem.title) {
      messages.push('Titre');
    }
    if (!this.newItem.pdf_id && !this.newItem.link) {
        messages.push('Un lien OU BIEN un pdf');
    }
    if (this.type == 'commAppeal' && !this.newItem.comm_appeal) {
        messages.push('Date limite de l\'appel à communication');
    }
    if (this.type == 'valAct' && !this.newItem.val_act) {
      messages.push('Titre de l\'acte de valorisation');
    }

  
    DATA.messages = messages;
    if (messages.length === 0) {
      this.newItem.debut = new Date(this.newItem.debut).getTime();
      this.newItem.end = new Date(this.newItem.end).getTime();
      this.newItem.comm_appeal = !this.newItem.comm_appeal && null || new Date(this.newItem.comm_appeal).getTime();
      
      const url = apiURL(this.newItem.table_name, 'create', '');
      ajax(url, 'POST', this.newItem, (res: any) => {
        DATA.title = 'Nouvel événement ajouté avec succès !';
        console.log(res);
        this.openBottomSheet();
      }, false);
    } else {
      DATA.title = 'Liste des champs à corriger :';
      this.openBottomSheet();
    }
  }

  openBottomSheet() {
    this.matBottomSheet.open(MatBottomSheetComponent);
  }

  constructor(private router: ActivatedRoute, private matBottomSheet: MatBottomSheet, private _adapter: DateAdapter<any>) { }
// if (!this.newItem.country_id) {this.newItem.region_id = null;}

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  ngOnInit() {
    this.router.params.subscribe(data => {
      this.type = data.type;

      this.newItem = new Event();
      if (this.type == 'commAppeal') {
        this.title = 'Ajouter un appel à communication';
      } else if (this.type == 'valAct') {
        this.title = 'Ajouter un acte de valorisation';
      } else {
        this.title = 'Ajouter une manifestation francophone';
      }
    });
    this._adapter.setLocale('fr');
  }

}
