import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ajax, apiURL } from '../../../providers/helpers';

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit {

  @Input() perPage: number;
  @Input() table_name: string;
  @Input() data: any;
  @Output() output = new EventEmitter<[]>();

  pages = [];
  total: number;

  constructor() { }

  paginate(total){     
    
    this.pages = [];
    if(total < 1) { return; }
    
    let startIndex = 0;
    const perPage = Number(this.perPage);
    
    while(startIndex < total) {
      this.pages.push({startIndex: startIndex, to: perPage});
      startIndex += perPage;
    }

    if (this.data.type) {
      this.pages = this.pages.map(page => {
        page.type = this.data.type;
        return page;
      }) 
    }
      
  }

  loadItems(page) {
    const url = apiURL(this.table_name, this.data.method, '');
    ajax(url, 'POST', page, (res: any) => {
      res = JSON.parse(res).records;
      this.output.emit(res);
    }, false);
  }

  ngOnInit() {
    const url = apiURL(this.table_name, 'count', '');
    ajax(url, 'POST', this.data, (res: any) => {
      res = JSON.parse(res);
      const total = parseInt(res.records);
      this.paginate(total);
      if(total > 0) {
        this.loadItems(this.pages[0]);
      }
    }, false);
  }

}
