import { Component, ViewChild, OnInit } from '@angular/core';
import { DateAdapter } from '@angular/material/core';
import { ajax, apiURL, dateForAMDInput } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';


interface JobAnnounceInTable {
  title: string;
  id: string;
  index: number;
}

interface SelectedJobAnnounce {
  type_id;
  description;
  is_approved;
  limit_date;
  title;
  img_id;
  organization_id;
  pdf_id;
  link;
  organization;
  thematics : any[];
  id;
  
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-job-announces',
  templateUrl: './job-announces.component.html',
  styleUrls: ['./job-announces.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class JobAnnouncesComponent implements OnInit {

  title: string = 'Politiques publiques';
  jobAnnounces = [];
  jobAnnounce_thematics = [];
  thematic_subs = [];
  job_announce_types = [];
  organizations = [];
  pdfs = [];
  imgs = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Titre', 'Organisme'];
  columnsToDisplay: string[] = ['title', 'organization_name'];
  expandedElement: JobAnnounceInTable | null;
  selectedJobAnnounce: SelectedJobAnnounce | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor(private _adapter: DateAdapter<any>) { }

  refresh() {
    this.table.renderRows();
  }

  editJobAnnounce(element) {
    if (this.selectedJobAnnounce.description.length < 1) {
      return;
    }else if (this.selectedJobAnnounce.description.length > 2000) {
      return;
    }
    if (this.selectedJobAnnounce.thematics.length < 1) {
      return;
    }
    if (!this.selectedJobAnnounce.pdf_id && !this.selectedJobAnnounce.link) {
       return;
    }
    if (!this.selectedJobAnnounce.limit_date) {
      return;
    }
    if (!this.selectedJobAnnounce.organization_id && !this.selectedJobAnnounce.organization) {
      return;
    }

    if (
      (!this.selectedJobAnnounce.pdf_id && !this.selectedJobAnnounce.link) || 
      (this.selectedJobAnnounce.pdf_id && this.selectedJobAnnounce.link)
    ) { 
        alert('Ajouter un lien OU un PDF.'); return; 
    }

    const thematic_sub_ids = this.selectedJobAnnounce.thematics.join(',');
    this.selectedJobAnnounce.limit_date = new Date(this.selectedJobAnnounce.limit_date).getTime();
    const url = apiURL('job_announce', 'update', '');

    ajax(url, 'POST', this.selectedJobAnnounce, (res: string) => {
      this.jobAnnounces[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : thematic_sub_ids, jobAnnounce_id: element.id};
      const url2 = apiURL('job_announce_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res2: any) => {
        this.expandedElement = null;
      }, false);
    }, false);
  }

  selectJobAnnounce(elementId) {
    const url = apiURL('job_announce', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedJobAnnounce = JSON.parse(res).records;
      this.selectedJobAnnounce.thematics = [];
      this.selectedJobAnnounce.id = elementId;
      if (this.selectedJobAnnounce.img_data) {
        this.selectedJobAnnounce.img_data = 'data:image/jpg;base64,' + this.selectedJobAnnounce.img_data;
      } else {
        this.selectedJobAnnounce.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }
      this.selectedJobAnnounce.limit_date = dateForAMDInput(this.selectedJobAnnounce.limit_date);
      const url2 = apiURL('job_announce_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        this.jobAnnounce_thematics = JSON.parse(res).records;
        this.selectedJobAnnounce.thematics = this.jobAnnounce_thematics.map(item => item.id);
      }, false);
      
    }, false);
  }

  setIndexToJobAnnounces(jobAnnounces) {
    if(!jobAnnounces || jobAnnounces.length < 1) return;
    jobAnnounces.forEach((jobAnnounce, index) => {
      jobAnnounce.index = index;
      jobAnnounce.author = jobAnnounce.author || 'SFSE';
      this.jobAnnounces.push(jobAnnounce);
    });
  }

  loadItems(jobAnnounces){
    this.jobAnnounces = [];
    this.setIndexToJobAnnounces(jobAnnounces);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  ngOnInit() {
    this._adapter.setLocale('fr');
  }
}
