import { Component, OnInit, ViewChild, Inject} from '@angular/core';
import { ajax, apiURL, exportCSVFile } from '../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {MatTable} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { MatConfirmComponent } from '../../mat-confirm.component';
import { DATA } from '../../../providers/data';

export interface SelectedThematicSub {
  name: string;
  thematic_name: string;
  thematic_id: string;
  position: string;
  id: string;
}

@Component({
  selector: 'app-thematics-sub',
  templateUrl: './thematics-sub.component.html',
  styleUrls: ['./thematics-sub.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class ThematicsSubComponent implements OnInit {
  @ViewChild(MatTable) table: MatTable<any>;
  thematicSubs   = [];
  newThematicSub = {};
  thematics = [];

  columnTitles: string[] = ['Nom', 'Thematique', 'Position'];
  columnsToDisplay: string[] = ['name', 'thematic_name', 'position'];
  expandedElement: any | null;
  selectedThematicSub: SelectedThematicSub | null;

  constructor(public dialog: MatDialog) {}

  openDialog(): void {
    const dialogRef = this.dialog.open(NewThematicSubComponent, {
      width: '300px'
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      this.newThematicSub = result;
    });
  }

  refresh() {
    this.table.renderRows();
  }

  edit(element) {
    const url = apiURL('thematic_sub', 'update', '');
    ajax(url, 'POST', this.selectedThematicSub, (res: any) => {
      this.thematicSubs[element.index] = this.selectedThematicSub;
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  selectThematicSub(elementId) {
    const url = apiURL('thematic_sub', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedThematicSub = JSON.parse(res);
    }, false);
  }

  deleteThematicSub(index) {
    const url = apiURL('thematic_sub', 'delete', '');
    ajax(url, 'POST', {id: this.selectedThematicSub.id}, (res: string) => {
      console.log(res);
      this.thematicSubs.splice(index, 1);
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  confirmDeleteThematicSub(index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'supprimer cette sous-thématique ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteThematicSub(index); }
    });
  }

  openCreateForm() {
    this.openDialog();
  }

  exportToCSV() {
    exportCSVFile(this.thematicSubs, 'SousThematiques');
  }

  ngOnInit() {
    const url = apiURL('thematic_sub', 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      this.thematicSubs = JSON.parse(res).records.map((thematicSub, index) => {
        thematicSub.index = index;
        return thematicSub;
      });
      this.table.renderRows();
    }, false);

    const thematicUrl = apiURL('thematic', 'read', '');
    ajax(thematicUrl, 'GET', null, (res: any) => {
      DATA.thematics = JSON.parse(res).records;
      this.thematics = DATA.thematics;
    }, false);
  }
}

export interface NewThematicSub {
  name: string;
  thematic_id: string;
  thematic_name: string;
  position: string;
}

@Component({
  selector: 'app-new-thematic-sub',
  templateUrl: 'new-thematic-sub.component.html',
  styleUrls: ['./thematics-sub.component.css'],
})
export class NewThematicSubComponent {
  newThematicSub: NewThematicSub = 
  {
    name: '', 
    thematic_id: '',
    thematic_name: '',
    position: ''
  };
  
  constructor(
    public dialogRef: MatDialogRef<NewThematicSubComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {}

  thematics = DATA.thematics;
  closeForm(): void {
    this.dialogRef.close();
  }

  addNewThematicSub() {
    const url = apiURL('thematic_sub', 'create', '');
    ajax(url, 'POST', this.newThematicSub, (res: any) => { 
      DATA.newThematicSub = this.newThematicSub;
    }, false);
  }
}
