import { Component, OnInit, ViewChild, Inject} from '@angular/core';
import { ajax, apiURL, autocomplete } from '../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {MatTable} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { DATA } from '../../../providers/data';

export interface SelectedOrganization {
  name: string;
  cat_id: string;
  id: string;
  verified: string;
  cat_name: string;
}


@Component({
  selector: 'app-organizations',
  templateUrl: './organizations.component.html',
  styleUrls: ['./organizations.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class OrganizationsComponent implements OnInit {
  @ViewChild(MatTable) table: MatTable<any>;
  organizations   = [];
  categories      = [];
  categoriesObj   = {};
  newOrganization = {};

  columnTitles: string[] = ['Nom', 'Catégorie', 'Vérifiée'];
  columnsToDisplay: string[] = ['name', 'cat_name', 'verified_value'];
  expandedElement: any | null;
  selectedOrganization: SelectedOrganization | null;

  constructor(public dialog: MatDialog) {}

  openDialog(): void {
    const dialogRef = this.dialog.open(NewOrganizationComponent, {
      width: '300px',
      data: {categories: this.categories}
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
      this.newOrganization = result;
    });
  }

  refresh() {
    this.table.renderRows();
  }

  edit(element) {
    const organizationUrl = apiURL('organization', 'update', '');
    ajax(organizationUrl, 'POST', this.selectedOrganization, (res: any) => {
      this.organizations[element.index] = this.selectedOrganization;
      this.organizations[element.index].cat_name = this.categoriesObj[this.selectedOrganization.cat_id];
      this.expandedElement = null;
    }, false);
  }

  selectOrganization(elementId, catName) {
    const organizationUrl = apiURL('organization', 'read_one', elementId);
    ajax(organizationUrl, 'GET', null, (res: any) => {
      this.selectedOrganization = JSON.parse(res);
      this.selectedOrganization.cat_name = catName;
    }, false);
  }

  openCreateForm() {
    this.openDialog();
  }

  ngOnInit() {
    const organizationUrl = apiURL('organization', 'read', '');
    ajax(organizationUrl, 'GET', null, (res: any) => {
      const organizations = JSON.parse(res).records;
      organizations.forEach((organization, index) => {
        organization.index = index;
        organization.verified_value = organization.verified === '1' ? 'oui' : 'non';
        this.organizations.push(organization);
      });
      this.table.renderRows();
    }, false);
    const categoryURL = apiURL('organization_category', 'read', '');
    ajax(categoryURL, 'GET', null, (res: any) => {
      this.categories = JSON.parse(res).records;
      this.categories.forEach(category => {
        this.categoriesObj[category.id] = category.name;
      });
    }, false);
  }
}

export interface NewOrganization {
  name: string;
  cat_id: string;
  verified: string;
  cat_name: string;
}

@Component({
  selector: 'app-new-organization',
  templateUrl: 'new-organization.component.html',
  styleUrls: ['./organizations.component.css'],
})
export class NewOrganizationComponent {
  newOrganization: NewOrganization = {name: '', cat_id: '', verified: '1', cat_name: ''};
  
  suggestions = this.data.categories.map(category => {
    if (category.accronym) {
      category.name = category.accronym + ' - ' + category.name;
    }
    return category.name;
  });

  suggestions_auto = [];
  constructor(
    public dialogRef: MatDialogRef<NewOrganizationComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {}

  closeForm(): void {
    this.dialogRef.close();
  }

  suggestResults() {
    this.suggestions_auto = autocomplete(this.newOrganization.cat_name, this.suggestions);
  }

  selectSuggestion(suggestion) {
    this.newOrganization.cat_name = suggestion.val;
    this.newOrganization.cat_id   = this.data.categories[suggestion.index].id;
    this.suggestions_auto = [];
  }

  addNewOrganization() {
    const organizationUrl = apiURL('organization', 'create', '');
    ajax(organizationUrl, 'POST', this.newOrganization, (res: any) => { 
      DATA.newOrganization = this.newOrganization;
    }, false);
  }

}
