import { Component, OnInit, ViewChild } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { AngularEditorConfig } from '@kolkov/angular-editor';

import { ajax, apiURL, resizeImage } from '../../../../providers/helpers';
import { MatTable } from '@angular/material';
import { MatDialog } from '@angular/material/dialog';
import { MatConfirmComponent } from '../../../mat-confirm.component';

interface StaffMemberInTable {
  id: string;
  name: string;
}

interface SelectedStaffMember {
  description: string;
  img: string;
  name: string;
  img_id: string;
  id: string;
  index: number;
}

@Component({
  selector: 'app-staff-members',
  templateUrl: './staff-members.component.html',
  styleUrls: ['./staff-members.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class StaffMembersComponent implements OnInit {

  @ViewChild(MatTable) table: MatTable<any>;
  @ViewChild('autosize') autosize: CdkTextareaAutosize;

  constructor(private dialog: MatDialog) {}
  
  config: AngularEditorConfig = {
    editable: true,
      spellcheck: true,
      height: 'auto',
      width: 'auto',
      translate: 'yes',
      enableToolbar: true,
      showToolbar: true,
      fonts: [
        {class: 'arial', name: 'Arial'},
        {class: 'times-new-roman', name: 'Times New Roman'},
        {class: 'calibri', name: 'Calibri'},
        {class: 'comic-sans-ms', name: 'Comic Sans MS'}
      ],
      customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    sanitize: true,
    toolbarPosition: 'top',
  };

  staffMembers = [];
  imgs = [];
  columnTitles: string[] = ['Nom'];
  columnsToDisplay: string[] = ['name'];
  expandedElement: StaffMemberInTable | null;
  selectedStaffMember: SelectedStaffMember | null;
  selectedMemberImg;

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  refresh() {
    this.table.renderRows();
  }

  editStaffMember() {
    const staffMemberUrl = apiURL('staff_member', 'update', '');
    ajax(staffMemberUrl, 'POST', this.selectedStaffMember, (res: string) => {
      this.staffMembers[this.selectedStaffMember.index] = JSON.parse(JSON.stringify(this.selectedStaffMember));
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  deleteStaffMember(index) {
    const staffMemberUrl = apiURL('staff_member', 'delete', '');
    ajax(staffMemberUrl, 'POST', {id: this.selectedStaffMember.id}, (res: string) => {
      console.log(res);
      this.staffMembers.splice(index, 1);
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  confirmDeleteStaffMember(index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'supprimer ce membre du conseil d\' administration ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteStaffMember(index); }
    });
  }

  selectStaffMember(element) {

    const staffMemberUrl = apiURL('staff_member', 'read_one', element.id);
    ajax(staffMemberUrl, 'GET', null, (res: any) => {

      this.selectedStaffMember = JSON.parse(res);
      this.selectedStaffMember.index = element.index;

      if (this.selectedStaffMember.img) {
        this.selectedMemberImg = 'data:image/jpg;base64,' + this.selectedStaffMember.img;
      }
    }, false);
  }



  ngOnInit() {
    const staffMembersURL = apiURL('staff_member', 'read_without_img', '');
    ajax(staffMembersURL, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      if (!res.records) {return;}
      const staffMembers = res.records;
      console.log(staffMembers);

      this.staffMembers = staffMembers.map((staffMember, index) => {
        staffMember.index = index;
        return staffMember;
      });
      this.refresh();
    }, false);
  }

}
