import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ajax, apiURL } from '../../../providers/helpers';
import { DATA } from '../../../providers/data';

@Component({
  selector: 'app-pdfsFolders',
  templateUrl: './pdfsFolders.component.html',
  styleUrls: ['./pdfsFolders.component.css']
})
export class PdfsFoldersComponent implements OnInit {
  private pdfFolders = [];
  stringifiedFolders;
  private newFolder;

  constructor(private router: Router) { }

  createFolder() {
    const pdfFolderURL = apiURL('pdf_folder', 'create', '');
    ajax(pdfFolderURL, 'POST', this.newFolder, res => {
      console.log(JSON.parse(res));
      const newFolder = JSON.parse(res).newFolder;
      newFolder.originalName = newFolder.name;
      this.pdfFolders.push(newFolder);
      this.newFolder = null;
    }, false);
  }

  loadFolders() {
    const pdfFolderURL = apiURL('pdf_folder', 'read', '');
    ajax(pdfFolderURL, 'GET', null, res => {
      let response: any = res;
      response = JSON.parse(response);
      this.pdfFolders = response.records.map(pdfFolder => {
        pdfFolder.originalName = pdfFolder.name;
        return pdfFolder;
      });
      this.stringifiedFolders = JSON.stringify(this.pdfFolders);
    }, false);
  }

  updateFolderName(imgFolder) {
    const imgFolderURL = apiURL('img_folder', 'update', imgFolder.id);
    ajax(imgFolderURL, 'POST', imgFolder, res => {
      imgFolder.originalName = imgFolder.name; 
    }, false);
  }

  goTo(folder){
    this.router.navigate(['/pdfsFolder', folder.id]);
    window.sessionStorage.pdfFolders = JSON.stringify(this.pdfFolders);
    window.sessionStorage.currentPdfFolder = JSON.stringify(folder);
  }

  ngOnInit() {
    this.loadFolders();
  }

}
