import { Component, OnInit, ViewChild } from '@angular/core';
import { DateAdapter } from '@angular/material/core';
import { ajax, apiURL, dateForAMDInput } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';


interface Project_appealInTable {
  title: string;
  limit_date: string;
  id: string;
  index: number;
}

interface SelectedProject_appeal {
  id;
  dimension_id; 
  organization_id;
  project_type_id;
  description;
  description_short;
  is_SFSE;
  limit_date; 
  title; 
  country_id; 
  img_id; 
  pdf_id; 
  region_id; 
  link;
  thematics: any[];
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-project-appeals',
  templateUrl: './project-appeals.component.html',
  styleUrls: ['./project-appeals.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})

export class ProjectAppealsComponent implements OnInit {

  title: string;
  dimensions = [];
  thematic_subs = []
  organizations = [];
  project_appeals = [];
  project_types = [];
  project_appeal_thematics = [];
  imgs = [];
  pdfs = [];
  regions = [];
  countrys = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Titre', 'Date limite'];
  columnsToDisplay: string[] = ['title', 'limit_date'];
  expandedElement: Project_appealInTable | null;
  selectedProject_appeal: SelectedProject_appeal | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor(private _adapter: DateAdapter<any>) { }

  refresh() {
    this.table.renderRows();
  }

  editProject_appeal(element) {
    this.selectedProject_appeal.limit_date = new Date(this.selectedProject_appeal.limit_date).getTime();
    this.selectedProject_appeal.thematic_sub_id = this.selectedProject_appeal.thematics.join(',');

    if (
      (!this.selectedProject_appeal.pdf_id && !this.selectedProject_appeal.link) || 
      (this.selectedProject_appeal.pdf_id && this.selectedProject_appeal.link)
    ) { 
        alert('Ajouter un lien OU un PDF.'); return; 
    }
    const url = apiURL('project_appeal', 'update', '');
    ajax(url, 'POST', this.selectedProject_appeal, (res: string) => {
      this.project_appeals[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : this.selectedProject_appeal.thematic_sub_id, project_appeal_id: element.id};
      const url2 = apiURL('project_appeal_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res: any) => {
        this.expandedElement = null;
      }, false);
    }, false);
  }

  selectProject_appeal(elementId) {
    const url = apiURL('project_appeal', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedProject_appeal = JSON.parse(res).records;
      this.selectedProject_appeal.thematics = [];
      this.selectedProject_appeal.id = elementId;
      if (this.selectedProject_appeal.img_data) {
        this.selectedProject_appeal.img_data = 'data:image/jpg;base64,' + this.selectedProject_appeal.img_data;
      } else {
        this.selectedProject_appeal.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }
      this.selectedProject_appeal.limit_date = dateForAMDInput(this.selectedProject_appeal.limit_date);

      const url2 = apiURL('project_appeal_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        this.project_appeal_thematics = JSON.parse(res).records;
        this.selectedProject_appeal.thematics = this.project_appeal_thematics.map(item => item.id);
      }, false);
      
    }, false);
  }

  setIndexToProject_appeals(project_appeals) {
    if(!project_appeals || project_appeals.length < 1) return;
    project_appeals.forEach((project_appeal, index) => {
      project_appeal.index = index;
      this.project_appeals.push(project_appeal);
    });
  }

  loadItems(project_appeals){
    this.project_appeals = [];
    this.setIndexToProject_appeals(project_appeals);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  ngOnInit() {
    this._adapter.setLocale('fr');
  }

}
