
import { Component, OnInit } from '@angular/core';
import { ajax, apiURL } from '../../../../providers/helpers';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DATA } from '../../../../providers/data';
import { MatBottomSheetComponent } from '../../../mat-bottom-sheet.component';
import { DateAdapter } from '@angular/material/core';


class Formation {
  table_name = 'formation';
  formation_type_id;
  pricing_id;
  description;
  description_short;
  is_approved = '1';
  is_highlighted = '0';
  is_online = '0';
  is_sfse = '0';
  organizator;
  place;
  title;
  country_id;
  img_id;
  pdf_id;
  region_id;
  debut;
  end;
  link;
  thematics = [];
}

@Component({
  selector: 'app-formation-create',
  templateUrl: './formation-create.component.html',
  styleUrls: ['./formation-create.component.css']
})
export class FormationCreateComponent implements OnInit {

  newItem;
  title = 'Nouvelle formation';
  thematic_subs = [];
  countrys = [];
  formation_types = [];
  pdfs = [];
  pricings = [];
  regions = [];
  imgs = [];

  hasNoDate: boolean;

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  createnewItem() {
    const messages = [];
    if(this.hasNoDate) {
      this.newItem.debut = null;
      this.newItem.end = null;
    } else {
      if (!this.newItem.debut) {
        messages.push('Date de début de la formation');
      }
      if (!this.newItem.end) {
        messages.push('Date de fin de la formation');
      }
    }

    if(this.newItem.is_online == '1') {
      this.newItem.debut = null;
      this.newItem.end = null;
    } else {
      if (!this.newItem.place) {
        messages.push('Lieu de la formation');
      }
      if (!this.newItem.country_id) {
        messages.push('Pays');
      }
      if(this.newItem.country_id == 73 && !this.newItem.region_id) {
        messages.push('Région en France');
      }
    }

    if (!this.newItem.formation_type_id) {
      messages.push('Type de formation');
    }
    if (!this.newItem.pricing_id) {
      messages.push('Tarif');
    }
    if (!this.newItem.description) {
      messages.push('Description du projet');
    }
    if (!this.newItem.description_short) {
      messages.push('Description courte');
    }
    if (!this.newItem.organizator) {
      messages.push('Organisme organisateur');
    }
    if (!this.newItem.title) {
      messages.push('Titre de la formation');
    }
    if (this.newItem.thematics.length < 1) {
      messages.push('Choisir au moins une thématique');
    }
    if ((!this.newItem.pdf_id && !this.newItem.link) || (this.newItem.pdf_id && this.newItem.link)) {
        messages.push('Un lien OU BIEN un pdf');
    }

    DATA.messages = messages;
    if (messages.length === 0) { 
      if (!this.hasNoDate) {
        this.newItem.debut = new Date(this.newItem.debut).getTime();
        this.newItem.end = new Date(this.newItem.end).getTime();
      }      
      this.newItem.thematics = this.newItem.thematics.join(',');

      const url = apiURL(this.newItem.table_name, 'create', '');
      ajax(url, 'POST', this.newItem, (res: any) => {
        console.log(res);
        const newFormaId = JSON.parse(res).records;
        const data = {formation_id: newFormaId, thematic_sub_id: this.newItem.thematics};
        const url2 = apiURL('formation_thematic_sub', 'create', '');
        ajax(url2, 'POST', data, (res2: any) => {
          console.log(res2);
          DATA.title = 'Nouvele formation ajouté avec succès !';
          this.newItem.thematics = ['', '', ''];
          this.openBottomSheet();
        }, false);
      }, false);
    } else {
      DATA.title = 'Liste des champs à corriger :';
      this.openBottomSheet();
    }
  }

  openBottomSheet() {
    this.matBottomSheet.open(MatBottomSheetComponent);
  }

  constructor(
    private matBottomSheet: MatBottomSheet,
    private _adapter: DateAdapter<any>
  ) {}

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  ngOnInit() {
    this.newItem = new Formation();
    this._adapter.setLocale('fr');
  }
}
