import { Component, ViewChild } from '@angular/core';
import { ajax, apiURL } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';


interface PublicPoliticInTable {
  title: string;
  id: string;
  index: number;
}

interface SelectedPublicPolitic {
  dimension_id;
  type_id;
  description;
  is_approved;
  title;
  country_id;
  img_id;
  region_id;
  pdf_id;
  link;
  place;
  thematics;
  id;
  
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-public-politics',
  templateUrl: './public-politics.component.html',
  styleUrls: ['./public-politics.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class PublicPoliticsComponent {

  title: string = 'Politiques publiques';
  publicPolitics = [];
  publicPolitic_thematics = [];
  thematic_subs = [];
  public_politic_types = [];
  public_politic_dimensions = [];
  regions = [];
  countrys = [];
  pdfs = [];
  imgs = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Titre'];
  columnsToDisplay: string[] = ['title'];
  expandedElement: PublicPoliticInTable | null;
  selectedPublicPolitic: SelectedPublicPolitic | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor() { }

  refresh() {
    this.table.renderRows();
  }

  editPublicPolitic(element) {
    if(this.selectedPublicPolitic.description.length > 2000) {
      return;
    }

    if(this.selectedPublicPolitic.dimension_id == '3') {
      this.selectedPublicPolitic.country_id = null;
      this.selectedPublicPolitic.region_id = null;
    } else if (!this.selectedPublicPolitic.country_id) {
      return;
    }

    if(this.selectedPublicPolitic.country_id == '73' && !this.selectedPublicPolitic.region_id) {
      return;
    }

    if (
      (!this.selectedPublicPolitic.pdf_id && !this.selectedPublicPolitic.link) || 
      (this.selectedPublicPolitic.pdf_id && this.selectedPublicPolitic.link)
    ) { 
        alert('Ajouter un lien OU un PDF.'); return; 
    }

    const thematic_sub_ids = this.selectedPublicPolitic.thematics.join(',');
    const url = apiURL('public_politic', 'update', '');

    ajax(url, 'POST', this.selectedPublicPolitic, (res: string) => {
      this.publicPolitics[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : thematic_sub_ids, publicPolitic_id: element.id};
      const url2 = apiURL('public_politic_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res2: any) => {
        this.expandedElement = null;
      }, false);
    }, false);
  }

  selectPublicPolitic(elementId) {
    const url = apiURL('public_politic', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedPublicPolitic = JSON.parse(res).records;
      this.selectedPublicPolitic.thematics = [];
      this.selectedPublicPolitic.id = elementId;
      if (this.selectedPublicPolitic.img_data) {
        this.selectedPublicPolitic.img_data = 'data:image/jpg;base64,' + this.selectedPublicPolitic.img_data;
      } else {
        this.selectedPublicPolitic.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }

      const url2 = apiURL('public_politic_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        this.publicPolitic_thematics = JSON.parse(res).records;
        this.selectedPublicPolitic.thematics = this.publicPolitic_thematics.map(item => item.id);
      }, false);
      
    }, false);
  }

  setIndexToPublicPolitics(publicPolitics) {
    if(!publicPolitics || publicPolitics.length < 1) return;
    publicPolitics.forEach((publicPolitic, index) => {
      publicPolitic.index = index;
      publicPolitic.author = publicPolitic.author || 'SFSE';
      this.publicPolitics.push(publicPolitic);
    });
  }

  loadItems(publicPolitics){
    this.publicPolitics = [];
    this.setIndexToPublicPolitics(publicPolitics);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

}
