import { Component, OnInit } from '@angular/core';
// import { COUNTRIES } from '../../../providers/globals';
import { ajax, apiURL } from '../../../providers/helpers';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DATA } from '../../../providers/data';
import { MatBottomSheetComponent } from '../../mat-bottom-sheet.component';

@Component({
  selector: 'app-account-create',
  templateUrl: './account-create.component.html',
  styleUrls: ['./account-create.component.css']
})
export class AccountCreateComponent implements OnInit {
  user = {
    civility_id: null,
    role_id: null,
    country: null,
    town: '',
    firstname: '',
    name: '',
    mail: '',
    pwd: '',
    phone: '',
    organization_id: null,
    status_id: null
  };

  pwdConfirm = '';
  showPwd = false;
  showPwdIcon = 'visibility';
  countries = [];
  roles;
  organizations;
  statuses;


  getRoles() {
    const url = apiURL('role', 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this.roles = res.records;
    }, false);
  }

  getOrganizations() {
    const url = apiURL('organization', 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this.organizations = res.records;
    }, false);
  }

  getStatuses() {
    const url = apiURL('user_status', 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this.statuses = res.records;
    }, false);
  }

  showHidePwd() {
    this.showPwd = !this.showPwd;
    this.showPwdIcon = this.showPwd ? 'visibility_off' : 'visibility';
  }

  createUser() {
    const messages = [];

    if (!this.user.civility_id) {
      messages.push('Civilité');
    }
    if (!this.user.role_id) {
      messages.push('Rôle');
    }
    if (!this.user.country) {
       messages.push('Pays');
    }
    if (this.user.town.trim().length <= 1) {
       messages.push('Nom de ville trop court');
    }
    if (this.user.firstname.trim().length <= 1) {
       messages.push('Prénom trop court');
    }
    if (this.user.name.trim().length <= 1) {
       messages.push('Nom de famille trop court');
    }
    if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.user.mail)) {
       messages.push('Adresse mail incorrecte');
    }
    if (this.user.pwd.trim().length <= 5) {
       messages.push('Le mot de passe doit faire au moins 6 caractères');
    } else if (this.user.pwd !== this.pwdConfirm) {
       messages.push('Les deux mots de passes doivent être identiques');
    }
    if (!/^(?:0|\(?\+33\)?\s?|0033\s?)[1-79](?:[\.\-\s]?\d\d){4}$/.test(this.user.phone)) {
       messages.push('Numéro de téléphone incorrect');
    }

    DATA.messages = messages;

    if (messages.length === 0) {
      const url = apiURL('user', 'create', '');
      ajax(url, 'POST', this.user, (res: any) => {
        DATA.title = 'Utilisateur créé avec succès !';
        this.openBottomSheet();
      }, false);
    } else {
      DATA.title = 'Liste des champs à corriger :';
      this.openBottomSheet();
    }
  }

  openBottomSheet() {
    this.matBottomSheet.open(MatBottomSheetComponent);
  }

  constructor(private matBottomSheet: MatBottomSheet) { }

  ngOnInit() {
    this.getRoles();
    this.getOrganizations();
    this.getStatuses();
  }

}
