import { Component, OnInit, ViewChild } from '@angular/core';
import { ajax, apiURL } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';
import { FormControl } from '@angular/forms';

import {MatTable} from '@angular/material';

interface WebToolInTable {
  title: string;
  author: string;
  id: string;
  index: number;
}

interface SelectedWebTool {
  type_id;
  author
  description;
  is_approved ;
  is_sfse;
  title;
  img_id;
  pdf_id;
  link;
  thematics : any[];
  id;
  goals;
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-webtools',
  templateUrl: './webtools.component.html',
  styleUrls: ['./webtools.component.css'], animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class WebtoolsComponent implements OnInit {

  title: string = 'Outils web';
  webTools = [];
  webTool_thematics = [];
  webTool_goals = [];
  web_tool_types = [];

  selectedGoals = new FormControl();
  thematic_subs = [];
  goals = [];
  pdfs = [];
  imgs = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Organisme publicateur', 'Titre'];
  columnsToDisplay: string[] = ['author', 'title'];
  expandedElement: WebToolInTable | null;
  selectedWebTool: SelectedWebTool | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor() { }

  refresh() {
    this.table.renderRows();
  }

  editWebTool(element) {
    const thematic_sub_id = this.selectedWebTool.thematics.join(',');
    if(!this.selectedGoals.value || this.selectedGoals.value.length < 1) {
      alert('Vous devez sélectionner ua moins un objectif');
      return;
    }
    if ((!this.selectedWebTool.pdf_id && !this.selectedWebTool.link) || (this.selectedWebTool.pdf_id && this.selectedWebTool.link)) { alert('Ajouter un lien OU un PDF.'); return; }
    const selectedGoals = this.selectedGoals.value.map(item => item.id).join(',');

    const url = apiURL('web_tool', 'update', '');

    ajax(url, 'POST', this.selectedWebTool, (res: string) => {
      this.webTools[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : thematic_sub_id, webTool_id: element.id};
      const url2 = apiURL('web_tool_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res2: any) => {
        const data2 = {goal_id : selectedGoals, webTool_id: element.id};
        const url3 = apiURL('web_tool_goal', 'update', '');
        ajax(url3, 'POST', data2, (res3: any) => {
          this.expandedElement = null;
        }, false);
      }, false);
    }, false);
  }

  selectWebTool(elementId) {
    const url = apiURL('web_tool', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedWebTool = JSON.parse(res).records;
      this.selectedWebTool.id = elementId;
      this.selectedWebTool.thematics = [];
      if (this.selectedWebTool.img_data) {
        this.selectedWebTool.img_data = 'data:image/jpg;base64,' + this.selectedWebTool.img_data;
      } else {
        this.selectedWebTool.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }

      const url2 = apiURL('web_tool_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        this.webTool_thematics = JSON.parse(res).records;
        this.selectedWebTool.thematics = this.webTool_thematics.map(item => item.id);
        const url2 = apiURL('web_tool_goal', 'read', elementId);
        ajax(url2, 'GET', null, (res2: any) => {
          this.webTool_goals = JSON.parse(res2).records;
        }, false);
      }, false);
      
    }, false);
  }

  setIndexToWebTools(webTools) {
    if(!webTools || webTools.length < 1) return;
    webTools.forEach((webTool, index) => {
      webTool.index = index;
      webTool.author = webTool.author || 'SFSE';
      this.webTools.push(webTool);
    });
  }

  loadItems(webTools){
    this.webTools = [];
    this.setIndexToWebTools(webTools);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  ngOnInit() {
  }

}
