import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ajax, apiURL } from '../../../providers/helpers';

@Component({
  selector: 'app-imagesFolders',
  templateUrl: './imagesFolders.component.html',
  styleUrls: ['./imagesFolders.component.css']
})
export class ImagesFoldersComponent implements OnInit {
  private imgFolders = [];
  stringifiedFolders;
  private newFolder;

  constructor(private router: Router) { }

  createFolder() {
    const imgFolderURL = apiURL('img_folder', 'create', '');
    ajax(imgFolderURL, 'POST', this.newFolder, res => {
      console.log(JSON.parse(res));
      const newFolder = JSON.parse(res).newFolder;
      newFolder.originalName = newFolder.name;
      this.imgFolders.push(newFolder);
      this.newFolder = null;
    }, false);
  }

  loadFolders() {
    const imgFolderURL = apiURL('img_folder', 'read', '');
    ajax(imgFolderURL, 'GET', null, res => {
      let response: any = res;
      response = JSON.parse(response);
      this.imgFolders = response.records.map(imgFolder => {
        imgFolder.originalName = imgFolder.name;
        return imgFolder;
      });
      this.stringifiedFolders = JSON.stringify(this.imgFolders);
    }, false);
  }

  updateFolderName(imgFolder) {
    const imgFolderURL = apiURL('img_folder', 'update', imgFolder.id);
    ajax(imgFolderURL, 'POST', imgFolder, res => {
      imgFolder.originalName = imgFolder.name; 
    }, false);
  }

  goTo(folder){
    this.router.navigate(['/imagesFolder', folder.id]);
    window.sessionStorage.imgFolders = JSON.stringify(this.imgFolders);
    window.sessionStorage.currentImageFolder = JSON.stringify(folder);
  }

  ngOnInit() {
    this.loadFolders();
  }

}
