import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';
import { ExportAsService, ExportAsConfig } from 'ngx-export-as';

import {ajax, apiURL} from '../../../providers/helpers';

import { MatConfirmComponent } from '../../mat-confirm.component';

class PDFDetail {
  id: string = null;
  name: string = null;
  folder_id: string = null;
  src: string = null;
  index: number = null;
}

@Component({
  selector: 'app-pdfs-folder-details',
  templateUrl: './pdfs-folder-details.component.html',
  styleUrls: ['./pdfs-folder-details.component.css']
})

export class PdfsFolderDetailsComponent implements OnInit {

  pdfs = [];
  pdfFolderId;
  newPdfSrc;
  pdfDetail = new PDFDetail();
  pdfFolders = JSON.parse(window.sessionStorage.pdfFolders);
  redirectFolderId;
  currentPdfFolder = JSON.parse(window.sessionStorage.currentPdfFolder);
  
  
  exportAsConfig: ExportAsConfig = {
    type: 'pdf', // the type you want to download
    elementId: 'pdf-viewer-dl', // the id of html/table element
  }

  constructor(
    private route : ActivatedRoute, 
    private location : Location, 
    private dialog: MatDialog,
    private exportAsService: ExportAsService) { }

  export(filename) {
    this.exportAsService.save(this.exportAsConfig, filename).subscribe(() => {});
  }
    
  resetPdfDetail() {
    this.pdfDetail = new PDFDetail();
  }

  loadPdfs() {
    const pdfURL = apiURL('pdf', 'read', '');
    ajax(pdfURL, 'POST', {folder_id: this.pdfFolderId}, res => {
  
      let response: any = res;
      response = JSON.parse(response);
      this.pdfs = response.records.map(pdf => {
        pdf.originalName = pdf.name;
        return pdf;
      });
      
    }, false);
  }

  displayPdfDetail(id, index) {
    const imgUrl = apiURL('pdf', 'read_one', id);
    ajax(imgUrl, 'GET', null, (res: string) => {
      res = JSON.parse(res);
      this.pdfDetail.src = 'data:application/pdf;base64,' + res['data'];
      this.pdfDetail.id = id;
      this.pdfDetail.index = index;
      this.pdfDetail.name = res['name'];
      
      }, false);
  }

  updatePdfName(pdf) {
    const pdfURL = apiURL('pdf', 'update', '');
    ajax(pdfURL, 'POST', pdf, () => {
      pdf.originalName = pdf.name; 
    }, false);
  }

  deleteFolder() {
    const pdfFolderURL = apiURL('pdf_folder', 'delete', '');
    ajax(pdfFolderURL, 'POST', {id: this.pdfFolderId}, () => {
      this.location.back();
    }, false);
  }

  confirmDeleteFolder(): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'effacer ce dossier et tout son contenu ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteFolder(); }
    });
  }
  
  confirmDeletePdf(pdfId, index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'effacer ce PDF ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deletePdf(pdfId, index); }
    });
  }
  
  onSubmitPDF(e) {
    e.preventDefault();

    const formData = new FormData(e.target);
    formData.append('folderId', this.pdfFolderId);
    
    const pdfURL = apiURL('pdf', 'create', '');
    ajax(pdfURL, 'POST', formData, res => {
      console.log(res);
      const pdfURL = apiURL('pdf', 'read_one', JSON.parse(res).newPdfId);
      ajax(pdfURL, 'GET', null, newPDF => {
        newPDF = JSON.parse(newPDF);
        newPDF.originalName = newPDF.name;
        newPDF.folder_id = this.pdfFolderId;
        
        this.pdfs.push(newPDF);
        this.newPdfSrc = null;
      }, false);
    }, true);
  }

  onFileSelected() {
    let $img: any = document.querySelector('#pdf-file');
   
    if (typeof (FileReader) !== 'undefined') {
      let reader = new FileReader();
   
      reader.onload = (e: any) => {
        this.newPdfSrc = e.target.result;
      };
   
      reader.readAsArrayBuffer($img.files[0]);
    }
  }

  deletePdf(pdfId, index) {
    const pdfURL = apiURL('pdf', 'delete', '');
    ajax(pdfURL, 'POST', {id: pdfId}, () => {
      this.pdfs.splice(index, 1);
    }, false);
  }

  moveToAnotherFolder() {
    const pdfURL = apiURL('pdf', 'update', '');
    ajax(pdfURL, 'POST', this.pdfDetail, () => {
      this.pdfs.splice(this.pdfDetail.index, 1);
    }, false);
  }

  ngOnInit() {
    this.route.paramMap.subscribe(params => {
      this.pdfFolderId = params.get('folderId');
      this.loadPdfs();
    });
    
 
  }

}
