import { Component, OnInit } from '@angular/core';

import { ajax, apiURL } from '../../../../providers/helpers';
import { DATA } from '../../../../providers/data';

import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { MatBottomSheetComponent } from '../../../mat-bottom-sheet.component';

class CaMember {
  declaration_of_interest: string;
  description: string;
  is_bureau_member: string;
  name: string;
  position: number;
  post: string;
}

@Component({
  selector: 'app-add-member',
  templateUrl: './add-member.component.html',
  styleUrls: ['./add-member.component.css']
})
export class AddMemberComponent implements OnInit {

  newCaMember = new CaMember();

  addCaMember() {
    const messages = [];

    if (!this.newCaMember.declaration_of_interest) {
      messages.push('Déclaration d\'intérêts');
    }
    if (!this.newCaMember.description) {
      messages.push('Description');
    }
    if (!this.newCaMember.is_bureau_member) {
       messages.push('Membre du bureau ou pas');
    }
    if (!this.newCaMember.name) {
       messages.push('Nom et prénom');
    }
    if (!this.newCaMember.position) {
       messages.push('Ordre dans la liste (position)');
    }
    if (!this.newCaMember.post) {
       messages.push('Poste occupé');
    }

    DATA.messages = messages;

    if (messages.length === 0) {
      const url = apiURL('ca_member', 'create', '');
      ajax(url, 'POST', this.newCaMember, (res: any) => {
        DATA.title = 'Membre du coseil d\'administration ajouté avec succès !';
        this.openBottomSheet();
      }, false);
    } else {
      DATA.title = 'Liste des champs à corriger :';
      this.openBottomSheet();
    }
  }

  openBottomSheet() {
    this.matBottomSheet.open(MatBottomSheetComponent);
  }

  constructor(private matBottomSheet: MatBottomSheet) { }

  ngOnInit() {
  }

}
