import { Component, OnInit, ViewChild } from '@angular/core';
import { ajax, apiURL } from '../../../../providers/helpers';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { DATA } from '../../../../providers/data';
import { MatBottomSheetComponent } from '../../../mat-bottom-sheet.component';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';

class PublicPolitic {
  table_name = 'public_politic';
  dimension_id;
  type_id;
  description = '';
  is_approved = '1';
  title;
  country_id;
  img_id;
  region_id;
  pdf_id;
  link;
  place;
  thematics = [];
}

@Component({
  selector: 'app-public-politic-create',
  templateUrl: './public-politic-create.component.html',
  styleUrls: ['./public-politic-create.component.css']
})

export class PublicPoliticCreateComponent implements OnInit {

  @ViewChild('autosize') autosize: CdkTextareaAutosize;
  newItem;
  title = 'Nouvelle politique publique';
  thematic_subs = [];
  public_politic_types = [];
  public_politic_dimensions = [];
  regions = [];
  countrys = [];
  pdfs = [];
  imgs = [];

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  createNewItem() {
    const messages = [];

    if (!this.newItem.dimension_id) {
      messages.push('Dimension de la politique (locale, régionale ou internationnale)');
    }
    if (this.newItem.dimension_id == 1 || this.newItem.dimension_id == 2){
      if (!this.newItem.country_id) {
        messages.push('Pays');
      }
      if (this.newItem.country_id == '73' && !this.newItem.region_id) {
        messages.push('Région');
      }
    }
    if (this.newItem.description.length < 1) {
      messages.push('Description du document');
    }else if (this.newItem.description.length > 2000) {
      messages.push('Description trop longue');
    }
    if (!this.newItem.type_id) {
      messages.push('Type de politique');
    }
    if (!this.newItem.title) {
      messages.push('Titre');
    }
    if (this.newItem.thematics.length < 1) {
      messages.push('Choisir au moins une thématique');
    }
    if ((!this.newItem.pdf_id && !this.newItem.link) || (this.newItem.pdf_id && this.newItem.link)) {
        messages.push('Un lien OU BIEN un pdf');
    }

    DATA.messages = messages;
    if (messages.length === 0) { 

      const thematics = this.newItem.thematics.join(',');

      const url = apiURL(this.newItem.table_name, 'create', '');
      ajax(url, 'POST', this.newItem, (res: any) => {
        console.log(res);
        const newDocId = JSON.parse(res).records;
        const data = {publicPolitic_id: newDocId, thematic_sub_id: thematics};
        const url2 = apiURL('public_politic_thematic_sub', 'create', '');
        ajax(url2, 'POST', data, (res2: any) => {
          console.log(res2);
          DATA.title = 'Nouveau document ajouté avec succès !';
          this.openBottomSheet();
        }, false);
      }, false);
    } else {
      DATA.title = 'Liste des champs à corriger :';
      this.openBottomSheet();
    }
  }

  openBottomSheet() {
    this.matBottomSheet.open(MatBottomSheetComponent);
  }

  constructor(private matBottomSheet: MatBottomSheet) {}

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  ngOnInit() {
    this.newItem = new PublicPolitic();
  }


}
