import { Component, ViewChild } from '@angular/core';
import { ajax, apiURL } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';


interface UsefulDocInTable {
  title: string;
  debut: string;
  organizator: string;
  id: string;
  index: number;
}

interface SelectedUsefulDoc {
  type_id;
  author
  description;
  is_approved ;
  is_sfse;
  title;
  img_id;
  pdf_id;
  link;
  thematics : any[];
  id;
  
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-useful-documentations',
  templateUrl: './useful-documentations.component.html',
  styleUrls: ['./useful-documentations.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class UsefulDocumentationsComponent {

  title: string = 'Documents utiles';
  usefulDocs = [];
  usefulDoc_thematics = [];
  thematic_subs = [];
  useful_doc_types = [];
  pdfs = [];
  imgs = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Organisme publicateur', 'Titre'];
  columnsToDisplay: string[] = ['author', 'title'];
  expandedElement: UsefulDocInTable | null;
  selectedUsefulDoc: SelectedUsefulDoc | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor() { }

  refresh() {
    this.table.renderRows();
  }

  editUsefulDoc(element) {
    this.selectedUsefulDoc.thematic_sub_id = this.selectedUsefulDoc.thematics.join(',');
    const url = apiURL('useful_doc', 'update', '');

    if (
      (!this.selectedUsefulDoc.pdf_id && !this.selectedUsefulDoc.link) || 
      (this.selectedUsefulDoc.pdf_id && this.selectedUsefulDoc.link)
    ) { 
        alert('Ajouter un lien OU un PDF.'); return; 
    }

    ajax(url, 'POST', this.selectedUsefulDoc, (res: string) => {
      this.usefulDocs[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : this.selectedUsefulDoc.thematic_sub_id, usefulDoc_id: element.id};
      const url2 = apiURL('useful_doc_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res2: any) => {
        this.expandedElement = null;
      }, false);
    }, false);
  }

  selectUsefulDoc(elementId) {
    const url = apiURL('useful_doc', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedUsefulDoc = JSON.parse(res).records;
      this.selectedUsefulDoc.thematics = [];
      this.selectedUsefulDoc.id = elementId;
      if (this.selectedUsefulDoc.img_data) {
        this.selectedUsefulDoc.img_data = 'data:image/jpg;base64,' + this.selectedUsefulDoc.img_data;
      } else {
        this.selectedUsefulDoc.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }

      const url2 = apiURL('useful_doc_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        console.log(res);
        this.usefulDoc_thematics = JSON.parse(res).records;
        this.selectedUsefulDoc.thematics = this.usefulDoc_thematics.map(item => item.id);
      }, false);
      
    }, false);
  }

  setIndexToUsefulDocs(usefulDocs) {
    if(!usefulDocs || usefulDocs.length < 1) return;
    usefulDocs.forEach((usefulDoc, index) => {
      usefulDoc.index = index;
      usefulDoc.author = usefulDoc.author || 'SFSE';
      this.usefulDocs.push(usefulDoc);
    });
  }

  loadItems(usefulDocs){
    this.usefulDocs = [];
    this.setIndexToUsefulDocs(usefulDocs);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }
}
