import { Component, OnInit, ViewChild} from '@angular/core';
import { ajax, apiURL } from '../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';

export interface UserInTable {
  mail: string;
  role_name: string;
  id: string;
  index: number;
}

export interface SelectedUser {
  country: string;
  firstname: string;
  name: string;
  phone: string;
  town: string;
  role_id: string;
  role_name: string;
}

@Component({
  selector: 'app-accounts',
  templateUrl: './accounts.component.html',
  styleUrls: ['./accounts.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})


export class AccountsComponent implements OnInit {
  @ViewChild(MatTable) table: MatTable<any>;

  users = [];
  columnTitles: string[] = ['Adresse mail', 'Rôle'];
  columnsToDisplay: string[] = ['mail', 'role_name'];
  expandedElement: UserInTable | null;
  selectedUser: SelectedUser | null;
  roles = [];
  rolesObj = {};

  constructor() {
  }

  refresh() {
    this.table.renderRows();
  }

  editUserRole(element) {
    const userUrl = apiURL('user', 'update', '');
    ajax(userUrl, 'POST', this.selectedUser, (res: any) => {
      this.users[element.index].role_name = this.rolesObj[this.selectedUser.role_id];
      this.expandedElement = null;
      // this.refresh();
    }, false);
  }

  selectUser(elementId, roleName) {
    const usersURL = apiURL('user', 'read_one', elementId);
    ajax(usersURL, 'GET', null, (res: any) => {
      this.selectedUser = JSON.parse(res);
      this.selectedUser.role_name = roleName;
    }, false);
  }

  setIndexToPosts(users) {
    users.forEach((user, index) => {
      user.index = index;
      this.users.push(user);
    });
  }

  loadItems(users){
    this.users = [];
    this.setIndexToPosts(users);
  }

  ngOnInit() {
    const rolesURL = apiURL('role', 'read', '');
    ajax(rolesURL, 'GET', null, (res: any) => {
      this.roles = JSON.parse(res).records;
      this.roles.forEach(role => {
        this.rolesObj[role.id] = role.name;
      });
    }, false);
  }
}
