import { Component, OnInit, ViewChild } from '@angular/core';
import { DateAdapter } from '@angular/material/core';
import { ajax, apiURL, dateForAMDInput } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';


interface FormationInTable {
  title: string;
  debut: string;
  organizator: string;
  id: string;
  index: number;
}

interface SelectedFormation {
  id;
  formation_type_id
  pricing_id;
  description;
  description_short;
  is_approved;
  is_highlighted;
  is_online;
  is_sfse;
  organizator;
  title;
  country_id;
  img_id;
  pdf_id;
  region_id;
  debut;
  end;
  link;
  place;
  thematics: any[];
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-formations',
  templateUrl: './formations.component.html',
  styleUrls: ['./formations.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class FormationsComponent implements OnInit {

  title: string = 'Formations';
  formations = [];

  thematic_subs = [];
  countrys = [];
  formation_types = [];
  formation_thematics = [];
  pdfs = [];
  pricings = [];
  regions = [];
  imgs = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Titre', 'Date de début', 'Organisateur'];
  columnsToDisplay: string[] = ['title', 'debut', 'organizator'];
  expandedElement: FormationInTable | null;
  selectedFormation: SelectedFormation | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor(private _adapter: DateAdapter<any>) { }

  refresh() {
    this.table.renderRows();
  }

  editFormation(element) {
    this.selectedFormation.debut = new Date(this.selectedFormation.debut).getTime();
    this.selectedFormation.end = new Date(this.selectedFormation.end).getTime();
    this.selectedFormation.thematic_sub_id = this.selectedFormation.thematics.join(',');
    if ((!this.selectedFormation.pdf_id && !this.selectedFormation.link) || (this.selectedFormation.pdf_id && this.selectedFormation.link)) { alert('Ajouter un lien OU un PDF.'); return; }
    const url = apiURL('formation', 'update', '');
    ajax(url, 'POST', this.selectedFormation, (res: string) => {
      this.formations[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : this.selectedFormation.thematic_sub_id, formation_id: element.id};
      const url2 = apiURL('formation_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res2: any) => {
        this.expandedElement = null;
      }, false);
    }, false);
  }

  selectFormation(elementId) {
    const url = apiURL('formation', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedFormation = JSON.parse(res).records;
      this.selectedFormation.thematics = [];
      this.selectedFormation.id = elementId;
      if (this.selectedFormation.img_data) {
        this.selectedFormation.img_data = 'data:image/jpg;base64,' + this.selectedFormation.img_data;
      } else {
        this.selectedFormation.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }

      this.selectedFormation.debut = dateForAMDInput(this.selectedFormation.debut);
      this.selectedFormation.end = dateForAMDInput(this.selectedFormation.end);

      const url2 = apiURL('formation_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        this.formation_thematics = JSON.parse(res).records;
        this.selectedFormation.thematics = this.formation_thematics.map(item => item.id);
      }, false);
      
    }, false);
  }

  setIndexToFormations(formations) {
    if(!formations || formations.length < 1) return;
    formations.forEach((formation, index) => {
      formation.index = index;
      this.formations.push(formation);
    });
  }

  loadItems(formations){
    this.formations = [];
    this.setIndexToFormations(formations);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  ngOnInit() {
    this._adapter.setLocale('fr');
  }

}
