import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { animate, state, style, transition, trigger } from '@angular/animations';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { AngularEditorConfig } from '@kolkov/angular-editor';

import { ajax, apiURL } from '../../../../providers/helpers';

import { MatTable } from '@angular/material';
import { MatDialog } from '@angular/material/dialog';
import { MatConfirmComponent } from '../../../mat-confirm.component';

interface PartnerInTable {
  id: string;
  name: string;
}

class SelectedPartner {
  id: string;
  name: string = ''; 
  index: string;
  img_id: string;
  pdf_id: string;
  link: string;
  img: string;
  table = 'partner';
}

@Component({
  selector: 'app-partners',
  templateUrl: './partners.html',
  styleUrls: ['./partners.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class PartnerComponent implements OnInit {

  @ViewChild(MatTable) table: MatTable<any>;
  @ViewChild('autosize') autosize: CdkTextareaAutosize;

  constructor(private dialog: MatDialog, private route : ActivatedRoute) {}

  config: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: 'auto',
    width: 'auto',
    translate: 'yes',
    enableToolbar: true,
    showToolbar: true,
    fonts: [
      {class: 'arial', name: 'Arial'},
      {class: 'times-new-roman', name: 'Times New Roman'},
      {class: 'calibri', name: 'Calibri'},
      {class: 'comic-sans-ms', name: 'Comic Sans MS'}
    ],
    customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    sanitize: true,
    toolbarPosition: 'top',
  };

  items = [];

  
  expandedElement;
  selectedItem;
  selectedItemImg;
  columnTitles: string[];
  columnsToDisplay: string[];

  imgOptions;
  pdfsOptions;

  refresh() {
    this.table.renderRows();
  }

  editItem() {
    const itemURL = apiURL(this.selectedItem.table, 'update', '');
    ajax(itemURL, 'POST', this.selectedItem, () => {
      this.items[this.selectedItem.index] = this.selectedItem;
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  deleteItem(index) {
    const url = apiURL(this.selectedItem.table, 'delete', '');
    ajax(url, 'POST', {id: this.selectedItem.id}, (res: string) => {
      console.log(res);
      this.items.splice(index, 1);
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  confirmDeleteItem(index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'supprimer cet item ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteItem(index); }
    });
  }

  selectItem(element) {
    const url = apiURL(this.selectedItem.table, 'read_one', element.id);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedItem = JSON.parse(res);
      this.selectedItem.table = 'partner';
      this.selectedItem.index = element.index;
      this.selectedItemImg =  this.selectedItem.img && 'data:image/jpg;base64,' + this.selectedItem.img || null;
      
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }
  
  ngOnInit(){
    this.selectedItem = new SelectedPartner();
    this.columnTitles = ['Nom'];
    this.columnsToDisplay = ['name'];
       
    const url = apiURL(this.selectedItem.table, 'read_without_img', '');
  
    ajax(url, 'GET', null, res => {

      let response = JSON.parse(res);
      const items = response.records;

      this.items = items.map((item, index) => {
        item.index = index;
        return item;
      });
      this.refresh();
    }, false);  
  }
}
