import { Component, ViewChild } from '@angular/core';
import { ajax, apiURL } from '../../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';

import {MatTable} from '@angular/material';


interface InfosDiversInTable {
  title: string;
  id: string;
  index: number;
}

interface SelectedInfosDivers {
  type_id;
  description;
  is_approved;
  title;
  img_id;
  pdf_id;
  link;
  thematics;
  id;
  
  thematic_sub_id: string;
  table_name;
  img_data;
}

@Component({
  selector: 'app-infos-divers',
  templateUrl: './infos-divers.component.html',
  styleUrls: ['./infos-divers.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})
export class InfosDiversComponent {

  title: string = 'Politiques publiques';
  infosDiverss = [];
  infosDivers_thematics = [];
  thematic_subs = [];
  infos_divers_types = [];
  pdfs = [];
  imgs = [];
  paginateData = {method:'read_without_img'};

  columnTitles: string[] = ['Titre'];
  columnsToDisplay: string[] = ['title'];
  expandedElement: InfosDiversInTable | null;
  selectedInfosDivers: SelectedInfosDivers | null;
  
  @ViewChild(MatTable) table: MatTable<any>;

  constructor() { }

  refresh() {
    this.table.renderRows();
  }

  editInfosDivers(element) {
    if(this.selectedInfosDivers.description.length < 1) {
      return;
    }
    if(this.selectedInfosDivers.title.length < 1) {
      return;
    }

    if ((!this.selectedInfosDivers.pdf_id && !this.selectedInfosDivers.link) || (this.selectedInfosDivers.pdf_id && this.selectedInfosDivers.link)) { alert('Ajouter un lien OU un PDF.'); return; }

    const thematic_sub_ids = this.selectedInfosDivers.thematics.join(',');
    const url = apiURL('infos_divers', 'update', '');

    ajax(url, 'POST', this.selectedInfosDivers, (res: string) => {
      this.infosDiverss[element.index] = JSON.parse(res);
      const data = {thematic_sub_id : thematic_sub_ids, infosDivers_id: element.id};
      const url2 = apiURL('infos_divers_thematic_sub', 'update', '');
      ajax(url2, 'POST', data, (res2: any) => {
        this.expandedElement = null;
      }, false);
    }, false);
  }

  selectInfosDivers(elementId) {
    const url = apiURL('infos_divers', 'read_one', elementId);
    ajax(url, 'GET', null, (res: any) => {
      this.selectedInfosDivers = JSON.parse(res).records;
      this.selectedInfosDivers.thematics = [];
      this.selectedInfosDivers.id = elementId;
      if (this.selectedInfosDivers.img_data) {
        this.selectedInfosDivers.img_data = 'data:image/jpg;base64,' + this.selectedInfosDivers.img_data;
      } else {
        this.selectedInfosDivers.img_data = '../../../../../assets/images/sfse-logo-menu.mng.png'
      }

      const url2 = apiURL('infos_divers_thematic_sub', 'read', elementId);
      ajax(url2, 'GET', null, (res: any) => {
        this.infosDivers_thematics = JSON.parse(res).records;
        this.selectedInfosDivers.thematics = JSON.parse(res).records.map(item => item.id);
      }, false);
      
    }, false);
  }

  setIndexToInfosDiverss(infosDiverss) {
    if(!infosDiverss || infosDiverss.length < 1) return;
    infosDiverss.forEach((infosDivers, index) => {
      infosDivers.index = index;
      this.infosDiverss.push(infosDivers);
    });
  }

  loadItems(infosDiverss){
    this.infosDiverss = [];
    this.setIndexToInfosDiverss(infosDiverss);
  }

  initList(tableName) {
    const url = apiURL(tableName, 'read', '');
    ajax(url, 'GET', null, (res: any) => {
      res = JSON.parse(res);
      this[tableName + 's'] = res.records;
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }
}
