import { GLOBALS } from './globals';

export const autocomplete = (txt, list) => {
    if (txt === '') { return []; }
    txt = txt.toLowerCase();
    
    return list
    .filter(elem => {
        if(elem.toLowerCase().match(txt)) {
            return elem;
        }
    }).map((elem, i) => {
        return {index : i, val : elem};
    });
};

export const apiURL = (object, method, id) => {
    return `${GLOBALS.API_URL}/-${object}/${method}.php?secure_key=${GLOBALS.API_CREDENTIALS}&id=${id}`;
};

export const ajax = (url, method, data, success, isImage) => {

    const req = new XMLHttpRequest();

    req.addEventListener('load', () => {

        if (req.status >= 200 && req.status < 400) {
            // const reponse = JSON.parse(req.responseText);
            success(req.response);
        } else {
            alert('une ereur est survenue');
            console.log('Une erreur est survenue. Statut de la requête  : ' + req.status);
            console.log(req.response);
        }

    }, true);

    req.open(method, url);

    req.addEventListener('error', e => {
        alert('une ereur est survenue');
        console.log(req, e);
    });

    if (data && !isImage) {
        data = JSON.stringify(data);
    }

    req.send(data);
};


export const resizeImage = (e, fileName, mimeType, quality, width) => {
    return new Promise( resolve => {
        const img = new Image();
        img.src = e.target.result;

        img.onload = () => {
            const ratio = img.width / img.height;
            const height = width / ratio;
            const elem = document.createElement('canvas');
            elem.width = width;
            elem.height = height;
            const ctx = elem.getContext('2d');
            // img.width and img.height will contain the original dimensions
            ctx.drawImage(img, 0, 0, width, height);
            ctx.canvas.toBlob((blob) => {
                const file = new File([blob], fileName, {
                    type: mimeType,
                    lastModified: Date.now()
                });
                resolve(file);
            }, mimeType, quality);
        }
    }); 
};

const convertToCSV = objArray => {
    var array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
    var str = '';

    for (let i = 0; i < array.length; i++) {
        let line = '';
        for (let index in array[i]) {
            if (line != '') line += ','

            line += array[i][index];
        }

        str += line + '\r\n';
    }

    return str;
};

export const exportCSVFile = (items, fileTitle) => {
    // Convert Object to JSON
    const jsonObject = JSON.stringify(items);
    const csv = convertToCSV(jsonObject);

    const exportedFilename = fileTitle + '.csv' || 'export.csv';

    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    if (window.navigator.msSaveBlob) { // IE 10+
        window.navigator.msSaveBlob(blob, exportedFilename);
    } else {
        const link = document.createElement("a");
        if (link.download !== undefined) { // feature detection
            // Browsers that support HTML5 download attribute
            const url = URL.createObjectURL(blob);
            link.setAttribute("href", url);
            link.setAttribute("download", exportedFilename);
            link.style.visibility = 'hidden';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }
    }
};


export const dateForAMDInput = date_ => {
    return new Date(Number(date_ + '000'));
}

export const stripHTML = html => {
    const doc = new DOMParser().parseFromString(html, 'text/html');
    return doc.body.textContent || "";
}
