import { Component, ViewChild, OnInit } from '@angular/core';

import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { AngularEditorConfig } from '@kolkov/angular-editor';

import { ajax, apiURL } from '../../../../providers/helpers';
import { DATA } from '../../../../providers/data';

import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { MatBottomSheetComponent } from '../../../mat-bottom-sheet.component';

class GeneralAssembly {
  pdf_id: string;
  title: string = '';
  tableName = 'general_assembly';
}

@Component({
  selector: 'app-create-general-assembly',
  templateUrl: './create-general-assembly.html',
  styleUrls: ['./create-general-assembly.css']
})
export class CreateGeneralAssemblyComponent implements OnInit {

  @ViewChild('autosize') autosize: CdkTextareaAutosize;

  config: AngularEditorConfig = {
    editable: true,
    spellcheck: true,
    height: 'auto',
    width: 'auto',
    translate: 'yes',
    enableToolbar: true,
    showToolbar: true,
    fonts: [
      {class: 'arial', name: 'Arial'},
      {class: 'times-new-roman', name: 'Times New Roman'},
      {class: 'calibri', name: 'Calibri'},
      {class: 'comic-sans-ms', name: 'Comic Sans MS'}
    ],
    customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    sanitize: true,
    toolbarPosition: 'top',
  };

  pdfsOptions;
  newItem;

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  addNewItem() {
    const messages = [];
   
    if (!this.newItem.title) {
      messages.push('Titre');
    }
    if (!this.newItem.pdf_id && !this.newItem.link) {
      messages.push('Un lien OU BIEN un pdf');
    }
    
    DATA.messages = messages;
    if (messages.length === 0) {
      const url = apiURL(this.newItem.tableName, 'create', '');
      ajax(url, 'POST', this.newItem, (res: any) => {
        DATA.title = 'Nouvel item ajouté avec succès !';
        this.openBottomSheet();
      }, false);
    } else {
      DATA.title = 'Liste des champs à corriger :';
      this.openBottomSheet();
    }
  }

  openBottomSheet() {
    this.matBottomSheet.open(MatBottomSheetComponent);
  }

  constructor(private matBottomSheet: MatBottomSheet) { }

  ngOnInit(){
    this.newItem = new GeneralAssembly();
  }
}
