import { Component, OnInit } from '@angular/core';

import { ViewChild, Inject} from '@angular/core';
import { ajax, apiURL } from '../../../providers/helpers';
import {animate, state, style, transition, trigger} from '@angular/animations';
import {MatTable} from '@angular/material';

export interface SelectedCategory {
  name: string;
  id: string;
  index: number;
}

@Component({
  selector: 'app-organizations-categories',
  templateUrl: './organizations-categories.component.html',
  styleUrls: ['./organizations-categories.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ]
})

export class OrganizationsCategoriesComponent implements OnInit {
  @ViewChild(MatTable) table: MatTable<any>;
  categories        = [];
  
  columnTitles: string[] = ['Nom'];
  columnsToDisplay: string[] = ['name'];
  expandedElement: any | null;
  selectedCategory  : SelectedCategory | null;

  newCategory       = {name:'', id: '', accronym: '', verified: '1'};
  
  constructor() { }

  refresh() {
    this.table.renderRows();
  }

  edit() {
    const categoryUrl = apiURL('organization_category', 'update', '');
    ajax(categoryUrl, 'POST', this.selectedCategory, (res: any) => {
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  delete() {
    const categoryUrl = apiURL('organization_category', 'delete', '');
    ajax(categoryUrl, 'POST', this.selectedCategory, (res: any) => {
      this.expandedElement = null;
      this.categories.splice(this.categories.indexOf(this.selectedCategory), 1);
      this.refresh();
    }, false);
  }

  addCategory() {
    if(this.newCategory.name.trim().length > 0) {
      const categoryUrl = apiURL('organization_category', 'create', '');
      ajax(categoryUrl, 'POST', this.newCategory, (res: any) => {
        this.newCategory.id = JSON.parse(res).id;
        this.categories.push(this.newCategory);
        this.newCategory = {name:'', id:'', verified: '1', accronym: ''};
        this.refresh();
      }, false);
    }
  }

  ngOnInit() {
    this.selectedCategory = null;
    const categoriesUrl = apiURL('organization_category', 'read', '');
    ajax(categoriesUrl, 'GET', null, (res: any) => {
      const categories = JSON.parse(res).records;
      categories.forEach(category => {
        this.categories.push(category);
      });
      this.table.renderRows();
    }, false);
  }

}

 

