import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { MatDialog } from '@angular/material/dialog';


import { ajax, apiURL, resizeImage } from '../../../providers/helpers';

import { MatConfirmComponent } from '../../mat-confirm.component';

class ImageDetail {
  img_id: string = null;
  name: string = null;
  folder_id: string = null;
  src: string = null;
  index: number = null;
}


@Component({
  selector: 'app-images-folder-details',
  templateUrl: './images-folder-details.component.html',
  styleUrls: ['./images-folder-details.component.css']
})

export class ImagesFolderDetailsComponent implements OnInit {

  imgFolders = JSON.parse(window.sessionStorage.imgFolders);
  currentImgFolder = JSON.parse(window.sessionStorage.currentImageFolder);
  redirectFolderId;
  imgFolderId;
  imageDetail = new ImageDetail();
  chosenImg: File;
  resizedImg: File;
  chosenImgSrc;
  resizedImgSrc;
  thumbnails = [];
  postImgURL = apiURL('img', 'create', '');
  constructor(private route : ActivatedRoute, private location : Location, private dialog: MatDialog) { }

  resetImageDetail() {
    this.imageDetail = new ImageDetail();
  }
  
  loadThumbnails() {
    const thumbnailsURL = apiURL('img_thumbnail', 'read', '');
    ajax(thumbnailsURL, 'POST', {folder_id: this.imgFolderId}, res => {
      let response: any = res;
      response = JSON.parse(response);
      this.thumbnails = response.records.map(thumbnail => {
        thumbnail.originalName = thumbnail.name;
        thumbnail.data = 'data:image/jpg;base64,' + thumbnail.data;
        return thumbnail;
      });
    }, false);
  }
    
  setImgToUpload(e) {
    this.chosenImg = e.target.files[0];
    const reader: FileReader = new FileReader();
    reader.readAsDataURL(this.chosenImg);

    reader.onloadend = (e: any) => {

      this.chosenImgSrc = reader.result;

      resizeImage(e, this.chosenImg.name, this.chosenImg.type, 0.2, 50).then((resizedImg: File) => {
        this.resizedImg = resizedImg;
        
        reader.readAsDataURL(this.resizedImg);
        reader.onloadend = () => {
          this.resizedImgSrc = reader.result;
        }
        
      });
    };
  }

  displayImageDetail(imgId, index) {
    const imgUrl = apiURL('img', 'read_one', imgId);
    ajax(imgUrl, 'GET', null, (res: string) => {
      res = JSON.parse(res);
      this.imageDetail.src = 'data:image/jpg;base64,' + res['data'];
      this.imageDetail.img_id = imgId;
      this.imageDetail.index = index;
      this.imageDetail.name = res['name'];
      
    }, false);
  }

  updateImgName(img) {
    const imgThumbnailUrl = apiURL('img_thumbnail', 'update', img.id);
    ajax(imgThumbnailUrl, 'POST', img, () => {
      img.originalName = img.name; 
    }, false);
  }

  deleteFolder() {
    const imgsURL = apiURL('img_folder', 'delete', '');
    ajax(imgsURL, 'POST', {id: this.imgFolderId}, () => {
      this.location.back();
    }, false);
  }

  confirmDeleteFolder(): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'effacer ce dossier et tout son contenu ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteFolder(); }
    });
  }
  
  confirmDeleteImg(imgId, index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'effacer cette image ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteImg(imgId, index); }
    });
  }
  
  onSubmitPostImage(e) {
    e.preventDefault();

    const formData = new FormData(e.target);
    formData.append('folderId', this.imgFolderId);
    formData.append('type', this.chosenImg.type);
    formData.append('img', this.resizedImg);
    
    const imgURL = apiURL('img', 'create', '');
    ajax(imgURL, 'POST', formData, res => {
      const imgThumbnailURL = apiURL('img_thumbnail', 'read_one', JSON.parse(res).newThumbnailId);
      ajax(imgThumbnailURL, 'GET', null, newThumbnail => {
        newThumbnail = JSON.parse(newThumbnail);
        newThumbnail.originalName = newThumbnail.name;
        newThumbnail.data = 'data:image/jpg;base64,' + newThumbnail.data;
        this.thumbnails.push(newThumbnail);
      }, false);
      this.chosenImgSrc = null;
    }, true);
  }

  deleteImg(imgId, index) {
    const imgURL = apiURL('img', 'delete', '');
    ajax(imgURL, 'POST', {id: imgId}, () => {
      this.thumbnails.splice(index, 1);
    }, false);
  }

  updateFolder() {
    const imgThumbnailURL = apiURL('img_thumbnail', 'update', '');
    ajax(imgThumbnailURL, 'POST', this.imageDetail, () => {
      this.thumbnails.splice(this.imageDetail.index, 1);
      this.resetImageDetail();
    }, false);
  }

  ngOnInit() {
    this.route.paramMap.subscribe(params => {
      this.imgFolderId = params.get('folderId');
      this.loadThumbnails();
    });
    
 
  }

}
