import { Component, OnInit, ViewChild } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { CdkTextareaAutosize } from '@angular/cdk/text-field';
import { AngularEditorConfig } from '@kolkov/angular-editor';

import { ajax, apiURL, resizeImage } from '../../../../providers/helpers';
import {MatTable} from '@angular/material';
import { MatDialog } from '@angular/material/dialog';
import { MatConfirmComponent } from '../../../mat-confirm.component';

interface CaMemberInTable {
  id: string;
  name: string;
  position: number;
  is_bureau_member: string;
}

interface SelectedCaMember {
  description: string;
  img: string;
  is_bureau_member: string;
  name: string;
  position: number;
  post: string;
  id: string;
  img_id: string;
  pdf_id: string;
  link: string;
  index: number;
}

@Component({
  selector: 'app-camembers',
  templateUrl: './camembers.component.html',
  styleUrls: ['./camembers.component.css'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({height: '0px', minHeight: '0'})),
      state('expanded', style({height: '*'})),
      transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
    ]),
  ],
})
export class CAMembersComponent implements OnInit {
  @ViewChild(MatTable) table: MatTable<any>;
  @ViewChild('autosize') autosize: CdkTextareaAutosize;

  constructor(private dialog: MatDialog) {}
  
  config: AngularEditorConfig = {
    editable: true,
      spellcheck: true,
      height: 'auto',
      width: 'auto',
      translate: 'yes',
      enableToolbar: true,
      showToolbar: true,
      fonts: [
        {class: 'arial', name: 'Arial'},
        {class: 'times-new-roman', name: 'Times New Roman'},
        {class: 'calibri', name: 'Calibri'},
        {class: 'comic-sans-ms', name: 'Comic Sans MS'}
      ],
      customClasses: [
      {
        name: 'quote',
        class: 'quote',
      },
      {
        name: 'titleText',
        class: 'titleText',
        tag: 'h1',
      },
    ],
    sanitize: true,
    toolbarPosition: 'top',
  };

  caMembers = [];

  imgOptions;
  pdfsOptions;
  
  columnTitles: string[] = ['Nom', 'Membre bureau', 'Position'];
  columnsToDisplay: string[] = ['name', 'is_bureau_member', 'position'];
  expandedElement: CaMemberInTable | null;
  selectedCaMember: SelectedCaMember | null;
  selectedMemberImg: string;

  refresh() {
    this.table.renderRows();
  }

  editCaMember() {
    const caMemberUrl = apiURL('ca_member', 'update', '');
    ajax(caMemberUrl, 'POST', this.selectedCaMember, (res: string) => {
      this.caMembers[this.selectedCaMember.index] = JSON.parse(JSON.stringify(this.selectedCaMember));
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  deleteCaMember(index) {
    const caMemberUrl = apiURL('ca_member', 'delete', '');
    ajax(caMemberUrl, 'POST', {id: this.selectedCaMember.id}, (res: string) => {
      this.caMembers.splice(index, 1);
      this.expandedElement = null;
      this.refresh();
    }, false);
  }

  confirmDeleteCaMember(index): void {
    const dialogRef = this.dialog.open(MatConfirmComponent, {
      width: '300px',
      data: {title : 'Attention !', actionTxt : 'supprimer ce membre du conseil d\' administration ?'}
    });

    dialogRef.afterClosed().subscribe(confirm => {
      if (confirm) { this.deleteCaMember(index); }
    });
  }

  selectCaMember(element) {
    const caMemberUrl = apiURL('ca_member', 'read_one', element.id);
    ajax(caMemberUrl, 'GET', null, (res: any) => {
      this.selectedCaMember = JSON.parse(res);
      this.selectedCaMember.index = element.index;

      if (this.selectedCaMember.img) {
        this.selectedMemberImg = 'data:image/jpg;base64,' + this.selectedCaMember.img;
      }
    }, false);
  }

  initItemOptions(item: string){
    
    const url = apiURL(item, 'read_without_data', '');
    ajax(url, 'GET', null, (res: any) => {
      
      const items = JSON.parse(res);
      const output = [];

      items.forEach((img, i) => {
        if(i ===  0 || img.folderName !== items[i-1].folderName) {
          const folder = {};
          folder[img.folderName] = [img];
          output.push(folder);
        } else {
          output[output.length-1][img.folderName].push(img);
        }
      });
      this.createItemOptions(item, output);
    }, false);
  }

  createItemOptions(item, inputItems) {
    item+='Options';
    this[item] = [];
    inputItems.forEach(obj => {
      for(const key in obj) {
        this[item].push({readonly : key});
        obj[key].forEach(elem => {
          this[item].push(elem);
        });
      }
    });
  }

  ngOnInit(){
    const caMembersURL = apiURL('ca_member', 'read', '');
    ajax(caMembersURL, 'GET', null, res => {
      let response: any = res;

      response = JSON.parse(response);
      const caMembers = response.records;

      this.caMembers = caMembers.map((caMember, index) => {
        caMember.index = index;
        return caMember;
      });
      this.refresh();
    }, false);
  }

}
