import { Component, Inject } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

@Component({
  selector: 'app-mat-confirm',
  template: `
    <h2 mat-dialog-title>{{ data.title }}</h2>
    <mat-dialog-content>Etes-vous sur de vouloir {{ data.actionTxt }}</mat-dialog-content>
    <div mat-dialog-actions>
      <button mat-button [mat-dialog-close]="false">Non</button>
      <button mat-button [mat-dialog-close]="true" cdkFocusInitial>Oui</button>
    </div>
  `,
  styles: ['']
})

export class MatConfirmComponent {
  constructor(
    public dialogRef: MatDialogRef<MatConfirmComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

}